.header__logo {
    width: 73px;
    height: 27px;
}

.header__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__menu-nav {
    display: flex;
    align-items: center;
}

.header__menu-links {
    display: block;
    padding: 28px 30px;
    /* font-family: 'Open Sans Bold', sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px; */
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.2s;
}

.header__menu-links:hover {
    background-color: rgb(47, 41, 41);
}

.header__theme-button {
    display: flex;
    align-items: center;
    gap: 20px;
}

.header__theme {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    width: 56px;
    height: 32px;
    background: #FFEC70;
    border-radius: 32px;
}

.header__theme-image {
    width: 20px;
    height: 20px;
}

.header__theme-circle {
    width: 24px;
    height: 24px;
    background: #FFFDED;
    border-radius: 12px;
}

#theme__sun {
    display: none;
}

.header__button {
    padding: 15px 25px;
    background: #925FF0;
    border-radius: 4px;
    color: #ffffff;
}

.theme__checkbox {
    display: none;
}

.theme__checkbox:checked~ .test .header .container .header__menu .header__theme-button .header__button {
    background-color: #292929;
}
.theme__checkbox:checked~ .test{
    background: #ffffff;
}


.test{
    background-color: black;
    width: 100%;
    height:10000px;
}

#header__logo-light {
    display: none;
}
.theme__checkbox:checked~ .test .header .container .header__menu .header__logo #header__logo-light {
    display: block;
}
.theme__checkbox:checked~ .test .header .container .header__menu .header__logo #header__logo-dark {
    display: none;
}
.theme__checkbox:checked~ .test .header .container .header__menu .header__logo #header__logo-dark {
    display: none;
}

.theme__checkbox:checked~ .test .header .container .header__menu .header__theme-button .header__theme {
    flex-direction:row-reverse;
    background: #051724;
}
.theme__checkbox:checked~ .test .header .container .header__menu .header__theme-button #theme__moon {
    display: none;
}
.theme__checkbox:checked~ .test .header .container .header__menu .header__theme-button #theme__sun {
    display: block;
}